          <td>　　减：境外所得（填写A108010）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="14_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">15</td>
          <td>　　加：纳税调整增加额（填写A105000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="15_1" v-model="form.a15_1" readonly></td>
        </tr>
        <tr>
          <td class="uk-text-center">16</td>
