          <td class="bg-unwrite uk-text-right"><input type="text" name="17_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">18</td>
          <td>　　加：境外应税所得抵减境内亏损（填写A108000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="18_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">19</td>
          <td>四、纳税调整后所得（13-14+15-16-17+18）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="19_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">20</td>
          <td>　　减：所得减免（填写A107020）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="20_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">21</td>
          <td>　　减：弥补以前年度亏损（填写A106000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="21_1" v-model="form.a21_1" readonly></td>
        </tr>
        <tr>
          <td class="uk-text-center">22</td>
          <td>　　减：抵扣应纳税所得额（填写A107030）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="22_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">23</td>
          <td>五、应纳税所得额（19-20-21-22）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="23_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">24</td>
          <td rowspan="13" class="uk-text-center">应纳税额计算</td>
          <td>　　税率（25%）</td>
          <td class="uk-text-right"><input type="text" name="24_1" readonly value="0.25"></td>
        </tr>
        <tr>
          <td class="uk-text-center">25</td>
          <td>六、应纳所得税额（23×24）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="25_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">26</td>
          <td>　　减：减免所得税额（填写A107040）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="26_1" v-model="form.a26_1" readonly></td>
        </tr>
        <tr>
          <td class="uk-text-center">27</td>
          <td>　　减：抵免所得税额（填写A107050）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="27_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">28</td>
          <td>七、应纳税额（25-26-27）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="28_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">29</td>
          <td>　　加：境外所得应纳所得税额（填写A108000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="29_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">30</td>
          <td>　　减：境外所得抵免所得税额（填写A108000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="30_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">31</td>
          <td>八、实际应纳所得税额（28+29-30）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="31_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">32</td>
          <td>　　减：本年累计实际已预缴的所得税额</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="32_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">33</td>
          <td>九、本年应补（退）所得税额（31-32）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="33_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">34</td>
          <td>　　其中：总机构分摊本年应补（退）所得税额（填写A109000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="34_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">35</td>
          <td>　　　　　财政集中分配本年应补（退）所得税额（填写A109000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="35_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">36</td>
          <td>　　　　　总机构主体生产经营部门分摊本年应补（退）所得税额（填写A109000）</td>
          <td class="bg-unwrite uk-text-right"><input type="text" name="36_1" readonly value="0.00"></td>
        </tr>
        </tbody>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 11,  //当前表格的名称编号
        setting: [],
        form: {
          'a1_1': '0.00', 'a2_1': '0.00', 'a4_1': '0.00', 'a5_1': '0.00', 'a6_1': '0.00',
          'a11_1': '0.00', 'a12_1': '0.00', 'a15_1': '0.00', 'a16_1': '0.00', 'a21_1': '0.00',
          'a26_1': '0.00'
        }
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
      this.formrevise()
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        this.setting = this.$store.state.companyCurrent.content.page11
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //第10行运算
        this.fn1(10, [1, 2, 3, 4, 5, 6, 7, 8, 9], 1)
        //第13行运算
        this.fn1(13, [10, 11, 12], 2)
        //第19行运算
        this.fn1(19, [13, 14, 15, 16, 17, 18], 3)
        //第23行运算
        this.fn1(23, [19, 20, 21, 22], 4)
        //第25行运算
        this.fn1(25, [23, 24], 5)
        //第28行运算
        this.fn1(28, [25, 26, 27], 4)
        //第30行运算
        this.fn1(31, [28, 29, 30], 2)
        //第33行运算
        this.fn1(33, [31, 32], 4)
      },
      // q  需计算单元格的行
      // gump 参与计算的行
      // type 公式{
      //  1:a=b+c+d+e+f+g+h-i-j
      //  2:a=b+c-d
      //  3:a=b-c+d-e-f-g
      //  4:a=b-c-d-...
      //  5:a=b*c
      // }
      fn1(q, gump, type) {
        if (J('input[name=' + gump[0] + '_1]').val()) {
          var sum = parseFloat(J('input[name=' + gump[0] + '_1]').val())
        } else {
          var sum = 0
        }
        for (let i = 1; i < gump.length; i++) {
          let dom = J('input[name=' + gump[i] + '_1]')
          if (dom.val()) {
            if (type == 1) {
              if (i != 7 && i !== 8) {
                sum -= parseFloat(dom.val())
              } else {
                sum += parseFloat(dom.val())
              }
            } else if (type == 2) {
              if (i != 1) {
                sum -= parseFloat(dom.val())
              } else {
                sum += parseFloat(dom.val())
              }
            } else if (type == 3) {
              if (i != 2 && i != 5) {
                sum -= parseFloat(dom.val())
              } else {
                sum += parseFloat(dom.val())
              }
            } else if (type == 4) {
              sum -= parseFloat(dom.val())
            } else if (type == 5) {
              sum *= parseFloat(dom.val())
            }
          }
        }
        J('input[name=' + q + '_1]').val(sum.toFixed(2))
      },
      formrevise() {
        var name1 = ''
        var name2 = ''
        var name3 = ''
        var name4 = ''
        var name5 = ''
        var name6 = ''
        //A101010
        if (this.$store.state.tableData[12]) {
          this.form.a1_1 = this.$store.state.tableData[12][0].value
          this.form.a11_1 = this.$store.state.tableData[12][15].value
        } else {
          name1 = '《一般企业收入明细表（101010）》'
        }
        //A102010
        if (this.$store.state.tableData[13]) {
          this.form.a2_1 = this.$store.state.tableData[13][0].value
          this.form.a12_1 = this.$store.state.tableData[13][15].value
        } else {
          name2 = '《一般企业成本支出明细表（102010）》'
        }
        //104000
        if (this.$store.state.tableData[14]) {
          this.form.a4_1 = this.$store.state.tableData[14][70].value
          this.form.a5_1 = this.$store.state.tableData[14][72].value
          this.form.a6_1 = this.$store.state.tableData[14][74].value
        } else {
          name3 = '《期间费用明细表（104000）》'
        }
        //105000
        if (this.$store.state.tableData[15]) {
          this.form.a15_1 = this.$store.state.tableData[15][122].value
          this.form.a16_1 = this.$store.state.tableData[15][123].value
        } else {
          name4 = '《纳税调整项目明细表（105000）》'
        }
        //106000
        if (this.$store.state.tableData[19]) {
          this.form.a21_1 = this.$store.state.tableData[19][46].value
        } else {
          name5 = '《企业所得税弥补亏损明细表（106000）》'
        }
        //107040
        if (this.$store.state.tableData[20]) {
          this.form.a26_1 = this.$store.state.tableData[20][37].value
        } else {
          name6 = '《减免所得税优惠明细表（107040）》'
        }
        if (name1 !== '' || name2 !== '' || name3 !== '' || name4 !== '' || name5 !== '' || name6 !== '') {
          this.eg('请按照填表顺序正确填写:' + name1 + name2 + name3 + name4 + name5 + name6 + '表未填写！')
        }
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    max-width: 900px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

  .bg-gray {
    background: #ddd;
  }
</style>



